.insurance {
    width: 100%;
    height: 70vh;
    position: relative;
    background: rgba(0, 0, 0, .3);
    margin-bottom: 1rem;
}


.insurance:before {
    content: '';
    position: absolute;
    background: url('../../assets/menu-bg.jpg') no-repeat center center/cover;
    height: 70vh;
    width: 100%;
    z-index: -1;
}

.insurance .content {
    position: absolute;
    top: 30%;
}

.insurance h1 {
    text-align: left;
    color: white;
}

.insurance p {
    color: #f8f8f8;
    margin: 1rem 0;
}

@media screen and (max-width: 940px) {

    .insurance .content {
        padding: 25px;
        width: 100%;
    }

    .insurance p {
        font-size: 14px;
    }

    .insurance h1 {
        font-size: 1.5rem;
        text-align: center;
    }
}